import React from 'react'
import './Navbar.css'
import { Squash as Hamburger } from 'hamburger-react'
import { useState } from 'react'
/* function clickburger(visible){
    if (visible=="hidden")setvisible("avail")
    if(visible=="avail")setvisible("hidden")
} */
function Navbar() {
    const [visible, setvisible] = useState("hidden")
    function clickburger(){
        if (visible=="hidden"){
            setvisible("how")
        }
        if(visible=="how")setvisible("hidden")
        alert("clicked")
    }
    return (
        <div class="navbar">
            <div class="navbar-container">
            <h1>Random</h1>
            <div class="big-screen">More features coming soon!</div>
            <div class="burger" onClick={clickburger}><Hamburger></Hamburger></div>
            <div class={visible}>
                <h1>More features coming soon!</h1>
            </div>
            </div>
        </div>
    )
}

export default Navbar
