import React from 'react'
import Output from './Output.js'
import { useState } from 'react'
import './Settings.js'
import './Settings.css'
import './Output.css'
function Settings() {
    const [visibility, setvisibility] = useState("hidden")
    const [numberofGroups, setnumberofGroups] = useState(1)
    const [element, setelem] = useState([])
    const onSubmit = (e) => {
        setnumberofGroups(e.target[2].value)
        const users = e.target[1].value.split("\n")
        while(users[users.length-1]=="")users.splice(users.length-1, 1)
        const ls = []
        let n=e.target[2].value
        ls.length=n;
        for(let i = 0; i<ls.length; i++){
            ls[i]=[];
        }
        let currentgroup = 0
        //assign leaders first
        const leaders=[]
        for(let i = 0; i<users.length; i++){
            if(users[i].includes("(L)")){
                leaders.push(users[i])
                users.splice(i, 1)
            }
        }
        for(let i = 0; i<leaders.length; i++){
            ls[currentgroup].push(leaders[i])
            currentgroup++
            if(currentgroup==n)currentgroup=0
        }
        //assign rest of members
        while (true){
            if(users.length==0)break
            else{
                let userindex = Math.floor(Math.random() * users.length)
                let user = users[userindex]
                ls[currentgroup].push(user)
                users.splice(userindex, 1)
                currentgroup++
                if (currentgroup==n){
                    currentgroup = 0
                }
            }
        }
        const elements = []
        for(let i = 0; i<ls.length; i++){
            let members=[];
            for( let j = 0; j<ls[i].length; j++){
                members.push(<li>{ls[i][j]}</li>)
                /* alert(ls[i][j]) */
            }
            elements.push(<div>
                Group {i+1}:<ol>{members}</ol>
                </div>)
        }
        e.preventDefault();
        setvisibility("avail")
        setelem(elements)
        setnumberofGroups(e.target[2].value)
      };
    return (
        <div>
            <div class={visibility}>
                <h1>Result: </h1>
                <div class="result-section">
                {element}
                </div>
            </div>
            <div class = "settings">
                <div class="inner-settings">
                    <h1>Edit Settings</h1>
                    <form method="get" onSubmit={onSubmit}>
                        <p>Dataset</p>
                        <select id="cars" name="cars"> <option value="volvo">Team generator</option> <option value="saab">Custom list</option> <option value="mercedes">Random picker</option> <option value="audi">Boolean</option></select>   
                        <h2>Items</h2>
                        <p>Items are split by new line. Please do not leave groups field empty, or 
                            the app will crash. Indicate team leaders by typing (L) after their name. For example albert (L)
                        </p>
                        
                        <textarea></textarea>
                        <p>Groups</p>
                        <input type="number" ></input><br/>
                        <button type="submit">Generate</button>
                    </form>
                </div>
                <div class="how-to-use">
                    <h1>How to use:</h1>
                    <ol>
                        <li>Add items to the text area</li>
                        <li>If you have team leaders, indicate it by typing (L) after the name</li>
                        <li>select the number of groups</li>
                        <li>Click on the generate button</li>
                        <li></li>
                    </ol>
                </div>
            </div>
        </div>
    )
}

export default Settings
